@media screen and (min-width: 320px) {
  .Header {
    display: flex;
    align-items: center;
    background-color: #422ed4;
    height: 60px;
    padding-left: 40px;
    padding-right: 40px;
  }
  .Img {
    margin-right: 10px;
  }
  .Text {
    font-family: Raleway;
    font-weight: 800;
    font-size: 16px;
    line-height: 1.2;
    color: #ffffff;
  }
}
@media screen and (min-width: 1200px) {
  .Header {
    padding-left: 160px;
    padding-right: 160px;
  }
}
